'use client';
import React, { useState } from 'react';
import { Button, Flex, Typography, Space, Spin, Card } from 'antd';
import {
  HeartFilled,
  StarFilled,
  ThunderboltFilled,
  CloseOutlined,
  UndoOutlined,
  SlidersOutlined,
  FireFilled,
} from '@ant-design/icons';
import { getCardStack, isProfile } from '@/utils/mockData';
import { SwipeCard } from './SwipeCard';

const { Title, Text } = Typography;

const Swipe: React.FC = () => {
  const [cards, setCards] = useState(getCardStack());
  // const [triggerSwipe, setTriggerSwipe] = useState<string | null>(null);
  const [triggerSwipe, setTriggerSwipe] = useState<"left" | "right" | null>(null);
  const [swipeHistory, setSwipeHistory] = useState<any[]>([]);

  const handleSwipes = (direction: string, card: any) => {
    setSwipeHistory((prev) => [...prev, { direction, card }]);
    setCards((prev) => prev.slice(0, -1));
  };



  const handleSwipe = (direction: "left" | "right") => {
    // if (!currentCard) return;

    // Add to swipe history for undo
    // setSwipeHistory([...swipeHistory, currentCard]);

    // // Only create matches for profile cards (not ads)
    // if (isProfile(currentCard) && direction === "right") {
    //   // Simulate a match (50% chance)
    //   if (Math.random() > 0.5) {
    //     const newMatch: Match = {
    //       id: `m${matches.length + 1}`,
    //       profile: currentCard,
    //       matchedAt: new Date(),
    //     };
    //     setMatches([newMatch, ...matches]);
    //     setMatchedProfile(currentCard);
    //     setMessages({
    //       ...messages,
    //       [currentCard.id]: []
    //     });
    //   }
    // }

    // // Track ad interactions
    // if (isAd(currentCard)) {
    //   if (direction === "right") {
    //     console.log(`Ad engagement: User interested in ${currentCard.brand}`);
    //     // In a real app, send analytics event
    //   } else {
    //     console.log(`Ad skipped: ${currentCard.brand}`);
    //   }
    // }

    // Remove the swiped card
    setCards(cards.slice(0, -1));
  };


  const handleUndo = () => {
    const lastSwipe = swipeHistory.pop();
    if (lastSwipe) {
      setCards((prev) => [...prev, lastSwipe.card]);
      setSwipeHistory([...swipeHistory]);
    }
  };

  return (
    <Flex vertical style={{ height: '100vh', background: '#0a0a12', maxWidth: 420, margin: '0 auto' }}>
      {/* Header */}
      <Flex justify="space-between" align="center" style={{ padding: '12px 16px', background: '#0a0a12' }}>
        <Title level={4} style={{ color: 'white', margin: 0 }}>
          Discover
        </Title>
        <Title level={5} style={{ color: 'white', margin: 0 }}>
          Nairobi
        </Title>
        <Button type="text" icon={<SlidersOutlined style={{ color: 'white', fontSize: 22 }} />} />
      </Flex>

      {/* Swipe Area */}
      <Flex flex={1} vertical justify="center" align="center" style={{ position: 'relative', overflow: 'hidden' }}>
        {cards.length === 0 ? (
          <Space direction="vertical" align="center" style={{ padding: 40 }}>
            <Flex
              align="center"
              justify="center"
              style={{
                width: 100,
                height: 100,
                borderRadius: '50%',
                background: 'linear-gradient(to right, rgba(236,72,153,0.2), rgba(239,68,68,0.2))',
              }}
            >
              <FireFilled style={{ fontSize: 42, color: '#ec4899' }} />
            </Flex>
            <Title level={3} style={{ color: 'white', marginBottom: 8 }}>
              You're all caught up!
            </Title>
            <Text style={{ color: '#aaa' }}>Check back later for new profiles</Text>
            <Button
              type="primary"
              shape="round"
              style={{
                background: 'linear-gradient(to right, #ec4899, #ef4444)',
                border: 'none',
                marginTop: 16,
              }}
              onClick={() => setCards(getCardStack())}
            >
              Refresh
            </Button>
          </Space>
        ) : (
          <div style={{ position: 'relative', width: '100%', height: '100%', padding: '1rem' }}>
            {cards.slice(-3).reverse().map((card, index) => {
              const isTopCard = index === 0;
              const cardStyle = {
                position: 'absolute' as const,
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                zIndex: 50 - index,
                transform: `scale(${1 - index * 0.03}) translateY(${index * -10}px)`,
                opacity: index === 0 ? 1 : 0.7,
              };

              if (isProfile(card)) {
                return (
                  <SwipeCard
                    key={card.id}
                    profile={card}
                    onSwipe={handleSwipe}
                    style={cardStyle}
                    triggerSwipe={isTopCard ? triggerSwipe : null}
                    onSwipeComplete={() => setTriggerSwipe(null)}
                  />
                );
              } else {
                return (
                  <AdCard
                    key={card.id}
                    ad={card}
                    onSwipe={handleSwipe}
                    style={cardStyle}
                    triggerSwipe={isTopCard ? triggerSwipe : null}
                    onSwipeComplete={() => setTriggerSwipe(null)}
                  />
                );
              }
            })}
          </div>
        )}
      </Flex>

      {/* Action Buttons */}
      {cards.length > 0 && (
        <Flex
          justify="center"
          align="center"
          style={{
            background: 'linear-gradient(to top, #0a0a12, rgba(10,10,18,0.9), transparent)',
            padding: '16px 0 24px',
            position: 'absolute',
            bottom: 0,
            width: '100%',
            zIndex: 20,
          }}
        >
          <Space size="large">
            <Button
              shape="circle"
              icon={<UndoOutlined style={{ color: '#f43f5e', fontSize: 22 }} />}
              style={{ width: 56, height: 56, background: 'white' }}
              onClick={handleUndo}
              disabled={swipeHistory.length === 0}
            />
            <Button
              shape="circle"
              icon={<CloseOutlined style={{ color: '#ef4444', fontSize: 28 }} />}
              style={{ width: 64, height: 64, background: 'white' }}
              onClick={() => setTriggerSwipe('left')}
            />
            <Button
              shape="circle"
              icon={<StarFilled style={{ color: '#3b82f6', fontSize: 22 }} />}
              style={{ width: 56, height: 56, background: 'white' }}
            />
            <Button
              shape="circle"
              icon={<HeartFilled style={{ color: 'white', fontSize: 28 }} />}
              style={{
                width: 64,
                height: 64,
                background: '#10b981',
                border: 'none',
              }}
              onClick={() => setTriggerSwipe('right')}
            />
            <Button
              shape="circle"
              icon={<ThunderboltFilled style={{ color: '#a855f7', fontSize: 22 }} />}
              style={{ width: 56, height: 56, background: 'white' }}
            />
          </Space>
        </Flex>
      )}
    </Flex>
  );
};

export default Swipe;
